import Vue from 'vue'
import Router from 'vue-router'/*引入vue-router*/
import VueResource from 'vue-resource'/*引入vue-resource*/
import Hello from '@/components/Hello'/*引入Hello*/
import Form from '@/components/Form'/*引入Form*/
import Card from '@/components/Card'/*引入Card*/
import Cart from '@/components/Cart'/*引入Cart*/

Vue.use(Router)/*宣告讓Vue使用Router*/
Vue.use(VueResource)/*宣告讓Vue使用VueResource*/

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/hello',/*網址阜號路徑*/
      name: 'Hello',/*名子*/
      component: Hello/*組件 同上方import進來的組件*/
    },
    {
      path: '/form',
      name: 'Form',
      component: Form
    },
    {
      path: '/card',
      name: 'Card',
      component: Card
    },
    {
      path: '/cart',
      name: '購物車',
      component: Cart
    }
  ]
})



