<template>
  <div id="app">
    <navbar></navbar><!--navbar導覽列組件-->
    <div class="text-center">
       <img src="./assets/logo.png"><!--讓Vue預設logo置中-->
    </div>

    <div class="container"><!--container區塊，裏頭顯示內容的組件用的-->
      <router-view></router-view><!--不同的container組件會透過<router-view>顯示-->
    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar'
export default {
  name: 'app',
  components:{
  Navbar
  }
}
</script>

<style lang="scss">
@import "../node_modules/bootstrap/scss/bootstrap";

.router-link-active {
  color: green !important;
}
</style>



