<template>
  <div>
    <div class="row">
      <div class="col-sm-8">
        <div class="card mb-3" v-for="(item, index) in products">
          <div class="card-img-top"
            style="height: 300px; background-size: cover; background-position: center center;"></div>
          <div class="card-block">
            <h4 class="card-title">Name</h4>
            <p class="card-text"></p>
          </div>
          <div class="card-footer text-center">
            <h4 class="card-title text-center">NT$ price</h4>
            <button
              type="button" class="btn btn-outline-primary">
              <span>已</span>
              選購
            </button>
          </div>
        </div>
      </div>
      <div class="col-sm-4">
        <!-- 購物車 -->
        <table class="table">
          <tr>
            <td>
              <button type="button" class="btn btn-outline-primary btn-sm">
                取消選購
              </button>
            </td>
            <td>
              <img class="card-img-top img-fluid" width="150" alt="Card image cap">
            </td>
            <td>
              品項
            </td>
            <td class="text-right">
              NT$ 單價
            </td>
          </tr>
          <tr>
            <td colspan="4" class="text-right">
              NT$ 總價
            </td>
          </tr>
        </table>
        <!-- 訂購資訊 -->
        <div v-if="temp.cart.length">
          <div class="form-group">
            <label for="email">Email address</label>
            <input type="email" class="form-control" id="email" placeholder="Enter email">
          </div>

          <div class="form-group">
            <label for="username">姓名</label>
            <input type="text" class="form-control" id="username" placeholder="輸入姓名">
          </div>

          <div class="form-group">
            <label for="address">地址</label>
            <input type="text" class="form-control" id="address" name="address" placeholder="請輸入地址">
          </div>

          <div class="text-right">
            <button class="btn btn-danger">送出</button>
          </div>
        </div>

      </div>
    </div>


  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      temp: {
        username: '',
        address: '',
        email: '',
        cart: []
      }
    }
  },
  methods: {//Vue裡面的行為，都可以往這邊放
    getData () {
      let self = this;
      // GET /someUrl
        this.$http.get('http://localhost:3000/products').then(response => {
          self.products = response.body
      // get body data
        console.log(response)
        // this.someData = response.body;
        });
    },
    toogleCart (item,i) {

    },
    submit (){

    }
  },
  computed:{
    showPrice (){

    }
  },
  mounted (){//初始化完成
    this.getData()//執行getData()
  }
}
</script>

